
import { ref, computed } from 'vue'

export default {
  setup () {
    const scrollPos = ref(window.scrollY)
    window.addEventListener('scroll', () => { scrollPos.value = window.scrollY })
    const didScroll = computed(() => scrollPos.value > 10)

    const currentWidth = ref(window.innerWidth)
    window.addEventListener('resize', () => { currentWidth.value = window.innerWidth })
    const isMobile = computed(() => currentWidth.value < 1024)

    const showNavContent = ref(false)

    return {
      didScroll,
      isMobile,
      showNavContent
    }
  }
}
