<template>
  <nav :class="didScroll || showNavContent ? 'bg-white shadow' : ''" class="fixed w-full z-30 top-0">
    <div class="w-full container mx-auto flex flex-wrap items-center justify-between mt-0 py-2">
      <div class="pl-4 flex items-center">
        <a :class="didScroll || showNavContent ? 'text-gray-800' : 'text-white'" class="font-bold text-2xl lg:text-4xl hover:underline" href="#">
          <slot name="nav-logo">
            <img class="h-10 inline" src="img/nav_logo.png" style="object-fit: contain;" />
          </slot>
          <slot name="nav-brand">
            MyCoolCompany
          </slot>
        </a>
      </div>

      <!-- Mobile menu -->
      <div v-show="isMobile" class="block pr-4">
        <button @click="showNavContent = !showNavContent" class="flex items-center p-1 hover:text-gray-900 focus:outline-none focus:shadow-outline transform transition hover:scale-105 duration-300 ease-in-out">
          <svg class="fill-current h-6 w-6" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg">
            <path d="M0 3h20v2H0V3zm0 6h20v2H0V9zm0 6h20v2H0v-2z" />
          </svg>
        </button>
      </div>
      <div v-show="!isMobile || showNavContent" :class="didScroll || showNavContent ? 'text-gray-800' : 'text-white'" class="w-full flex-grow lg:flex lg:items-center lg:w-auto mt-2 lg:mt-0 bg-white lg:bg-transparent p-4 lg:p-0 z-20">
        <ul class="list-reset lg:flex justify-end flex-1 items-center" @click="showNavContent = false">
          <slot name="nav-links">
            <li class="mr-3">
              <a class="inline-block hover:underline py-2 px-4 font-bold" href="#">Active</a>
            </li>
            <li class="mr-3">
              <a class="inline-block hover:underline py-2 px-4" href="#">Services</a>
            </li>
            <li class="mr-3">
              <a class="inline-block hover:underline py-2 px-4" href="#">FAQ</a>
            </li>
          </slot>
        </ul>
          <button
            @click="showNavContent = false"
            :class="didScroll || showNavContent ? 'gradient text-white' : 'bg-white text-gray-800'"
            class="mx-auto lg:mx-0 hover:underline font-bold rounded-full mt-4 lg:mt-0 py-4 px-8 shadow opacity-75 focus:outline-none focus:shadow-outline transform transition hover:scale-105 duration-300 ease-in-out"
          >
            <slot name="nav-call-to-action">
              <a href="#">To Action!</a>
            </slot>
          </button>
      </div>
    </div>
    <hr class="border-b border-gray-100 opacity-25 my-0 py-0" />
  </nav>
</template>

<script lang='ts'>
import { ref, computed } from 'vue'

export default {
  setup () {
    const scrollPos = ref(window.scrollY)
    window.addEventListener('scroll', () => { scrollPos.value = window.scrollY })
    const didScroll = computed(() => scrollPos.value > 10)

    const currentWidth = ref(window.innerWidth)
    window.addEventListener('resize', () => { currentWidth.value = window.innerWidth })
    const isMobile = computed(() => currentWidth.value < 1024)

    const showNavContent = ref(false)

    return {
      didScroll,
      isMobile,
      showNavContent
    }
  }
}
</script>
